<template>
	<div id="statcenter-paytype-detail">
		<public-header title="详情"></public-header>
		<div class="content padding-15 font-14 ">
			<van-row class="c-gray">
				<van-col span="5" class="padding-10-tb">账期</van-col>
				<van-col span="3" class="padding-10-tb">总单数</van-col>
				<van-col span="4" class="padding-10-tb">货到付款</van-col>
				<van-col span="4" class="padding-10-tb">余额支付</van-col>
				<van-col span="4" class="padding-10-tb">微信支付</van-col>
				<van-col span="4" class="padding-10-tb">支付宝</van-col>
				<van-col span="5" class="padding-10-tb"></van-col>
				<van-col span="3" class="padding-10-tb"></van-col>
				<van-col span="4" class="padding-10-tb">单数/占比/</van-col>
				<van-col span="4" class="padding-10-tb">单数/占比</van-col>
				<van-col span="4" class="padding-10-tb">单数/占比/</van-col>
				<van-col span="4" class="padding-10-tb">单数/占比/</van-col>
				<van-col span="5" class="padding-10-tb"></van-col>
				<van-col span="3" class="padding-10-tb"></van-col>
				<van-col span="4" class="padding-10-tb">成交金额</van-col>
				<van-col span="4" class="padding-10-tb">成交金额</van-col>
				<van-col span="4" class="padding-10-tb">成交金额</van-col>
				<van-col span="4" class="padding-10-tb">成交金额</van-col>
			</van-row>
			<van-row v-if='0'>
				<van-col span="6">总计</van-col>
				<van-col span="5">{{stat.total_success_order}}</van-col>
				<van-col span="5">{{stat.total_fee}}</van-col>
				<van-col span="5">{{stat.total_final_fee}}</van-col>
				<van-col span="3">{{stat.total_refund_fee}}</van-col>
			</van-row>
			<van-row v-for="(item, index) in detail" :key="index" v-if="detail">
				<van-col span="5">{{item.stat_day}}</van-col>
				<van-col span="3">{{item.total_success_order}}</van-col>
				<van-col span="4">{{item.order_delivery + '/' + item.pre_order_delivery + '%'}}</van-col>
				<van-col span="4">{{item.order_credit + '/' + item.pre_order_credit + '%'}}</van-col>
				<van-col span="4">{{item.order_wechat + '/' + item.pre_order_wechat + '%'}}</van-col>
				<van-col span="4">{{item.order_alipay + '/' + item.pre_order_alipay + '%'}}</van-col>
				<van-col span="5"></van-col>
				<van-col span="3"></van-col>
				<van-col span="4">￥{{item.sum_delivery}}</van-col>
				<van-col span="4">￥{{item.sum_credit}}</van-col>
				<van-col span="4">￥{{item.sum_wechat}}</van-col>
				<van-col span="4">￥{{item.sum_alipay}}</van-col>
			</van-row>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import {mapState} from 'vuex'
	import publicHeader from "@/components/header"
	import {getData} from "../../controller/funcCommon.js"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				showPreLoading: true,
				stat: {},
				detail: []
			}
		},
		methods: {
			onLoad() {
				let that = this;
				getData({
					vue: this,
					data: {
						detail: 1,
						filter: that.filter ? that.filter.items: {}
					},
					url: 'plateform/statcenter/paytype/index',
					success: function(result) {
						that.detail = result.detail;
						that.stat = result.stat;
					}
				});
			}
		},
		computed: {
			...mapState([
				'filter',
			]),
		},
		mounted() {
			this.onLoad();
		},

	}
</script>

<style scoped>
	#statcenter-paytype-detail .van-row{
		font-size: 12px;
		text-align: center;
	}
	#statcenter-paytype-detail .van-row:nth-child(odd) {
		background-color: #ddd;
	}
	#statcenter-paytype-detail .van-row .van-col{
		padding: 8px 0;
		border-right: 1px solid #eee;
	}
	#statcenter-paytype-detail .van-row .van-col:last-child{
		border-right: 0px;
	}
</style>


